/* react-tooltip library styles override */

.__react_component_tooltip.show {
    opacity: 0.64 !important;
}

.isOpen {
    position: fixed;
    z-index: 8;
    -webkit-transform: translateX(0);
            transform: translateX(0);
    width: 400px;
    height: 100%;
    box-shadow: 0 8px 24px -16px rgba(170, 170, 170, 0.48);
    /*transition: transform 0.6s ease;*/
}

.isOpen .vertical {
    display: none;
}

.isClose .horizontal {
    display: none;
}

.isClose {
    position: fixed;
    z-index: 1;
    -webkit-transform: translateX(calc(-100% + 63px));
            transform: translateX(calc(-100% + 63px));
    width: 64px;
    height: 100%;
    box-shadow: 0 8px 24px -16px rgba(170, 170, 170, 0.48);
    /*transition: transform 0.6s ease;*/
}

.openSideMenu {
    width: 100%;
    padding: 0 0 0 400px;
    max-width: 100% !important;
}

.openSideMenu main {
    padding: 0 50px 0 50px;
}

.closedSideMenu {
    padding: 0 0 0 50px;
    width: 100%;
}

.closedSideMenu main {
    padding: 0 50px 0 120px;
}


