/* --------- Tooltip Styles ---------- */

.Menu_tooltip__1sk2c::before,
.Menu_tooltip__1sk2c::after {
    position: absolute;
    left: 50%;
    opacity: 0;
    -webkit-transition: all ease 0.3s;
    transition: all ease 0.3s;
}

.Menu_tooltip__1sk2c::before {
    content: '';
    border-width: 7px 8px 7px 0px;
    border-style: solid;
    border-color: transparent #007d8d transparent transparent;
    top: 36px;
    left: 105px;
}

.Menu_tooltip__1sk2c::after {
    content: attr(data-tooltip);
    background: #007d8d;
    color: #fff;
    font-size: 14px;
    border-radius: 8px;
    left: 113px;
    top: 22.5px;
    padding: 10px 14px;
    white-space: nowrap;
    z-index: 1;
}


/* Hover states */

.Menu_tooltip__1sk2c:hover::before,
.Menu_tooltip__1sk2c:hover::after {
    opacity: 1;
}


